body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main{
  flex: 1 1;
  flex-direction: row;
  display: flex;
  justify-content: center;
  align-items: center;
}
.title-space{
  flex: 3 1;
  flex-direction: column;
  color: whitesmoke;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.menu-space{
  flex: 4 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.screen{
  line-height: 1.4;
  border-radius: 0px 15px 0px;
  border: 1px #58D3F7 solid;
  width: 400px;
  padding:10px;
  margin: 15px;
  background-color: rgba(88, 211, 247, 0.2);
  color: #58D3F7;
  height: 80%;
}
.screen.animate {
  -webkit-animation: fadein-screen 1s;
          animation: fadein-screen 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.screen.exit {
  -webkit-animation: fadeout-screen 1s;
          animation: fadeout-screen 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.button-style {
  line-height: 1.4;
  border-radius: 0px 15px 0px;
  border: 1px #58D3F7 solid;
  width: 400px;
  padding:10px;
  margin: 15px;
  background-color: rgba(88, 211, 247, 0.2);
  color: #58D3F7;
  height: 80%;
  width: 80%;
  height: 10%;
  opacity: 0;
  cursor: pointer;
}
button {
  line-height: 1.4;
  border-radius: 0px 15px 0px;
  border: 1px #58D3F7 solid;
  width: 400px;
  padding:10px;
  margin: 15px;
  background-color: rgba(88, 211, 247, 0.2);
  color: #58D3F7;
  height: 80%;
  width: 80%;
  height: 15%;
  opacity: 0;
  cursor: pointer;
}
button.animate {
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
button.exit {
  -webkit-animation: fadeout 1s;
          animation: fadeout 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
button:active {
  background-color: aliceblue;
}
button:hover {
  background-color: rgba(88, 211, 247, 0.5);
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

button:nth-child(1) { -webkit-animation-delay: 0.2s; animation-delay: 0.2s; }
button:nth-child(2) { -webkit-animation-delay: 0.4s; animation-delay: 0.4s; }
button:nth-child(3) { -webkit-animation-delay: 0.6s; animation-delay: 0.6s; }
button:nth-child(4) { -webkit-animation-delay: 0.8s; animation-delay: 0.8s; }
button:nth-child(5) { -webkit-animation-delay: 1.0s; animation-delay: 1.0s; }
button:nth-child(6) { -webkit-animation-delay: 1.2s; animation-delay: 1.2s; }

