@import url('https://fonts.googleapis.com/css?family=Exo');
@import url('https://fonts.googleapis.com/css?family=Maven+Pro');


.main{
  flex: 1;
  flex-direction: row;
  display: flex;
  justify-content: center;
  align-items: center;
}
.title-space{
  flex: 3;
  flex-direction: column;
  color: whitesmoke;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.menu-space{
  flex: 4;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.screen{
  line-height: 1.4;
  border-radius: 0px 15px 0px;
  border: 1px #58D3F7 solid;
  width: 400px;
  padding:10px;
  margin: 15px;
  background-color: rgba(88, 211, 247, 0.2);
  color: #58D3F7;
  height: 80%;
}
.screen.animate {
  animation: fadein-screen 1s;
  animation-fill-mode: forwards;
}
.screen.exit {
  animation: fadeout-screen 1s;
  animation-fill-mode: forwards;
}

button {
  line-height: 1.4;
  border-radius: 0px 15px 0px;
  border: 1px #58D3F7 solid;
  width: 400px;
  padding:10px;
  margin: 15px;
  background-color: rgba(88, 211, 247, 0.2);
  color: #58D3F7;
  height: 80%;
  width: 80%;
  height: 10%;
  opacity: 0;
  cursor: pointer;
}
button.animate {
  animation: fadein 1s;
  animation-fill-mode: forwards;
}
button.exit {
  animation: fadeout 1s;
  animation-fill-mode: forwards;
}
button:active {
  background-color: aliceblue;
}
button:hover {
  background-color: rgba(88, 211, 247, 0.5);
  transform: translateY(-10px);
}

button:nth-child(1) { animation-delay: 0.2s; }
button:nth-child(2) { animation-delay: 0.4s; }
button:nth-child(3) { animation-delay: 0.6s; }
button:nth-child(4) { animation-delay: 0.8s; }
button:nth-child(5) { animation-delay: 1.0s; }
button:nth-child(6) { animation-delay: 1.2s; }

h4, h3, h2, h1, p{
  margin-top: 0;
  font-family: 'Maven Pro', sans-serif;
}

a, a:visited, a:hover, a:active {
  color: inherit;
  text-decoration: none;
  font-family: 'Maven Pro', sans-serif;
}

@keyframes fadein {
  0% { 
    opacity: 0; 
    transform: translateX(250px);
  }
  100% { 
    opacity: 1; 
    transform: translateX(0px);
  }
}

@keyframes fadeout {
  100% { 
    opacity: 0; 
    transform: translateX(250px);
  }
  0% { 
    opacity: 1; 
    transform: translateX(0px);
  }
}

@keyframes fadein-screen {
  0% { 
    opacity: 0; 
    transform: translateX(-200px);
  }
  100% { 
    opacity: 1; 
    transform: translateX(0px);
  }
}

@keyframes fadeout-screen {
  100% { 
    opacity: 0; 
    transform: translateX(-200px);
  }
  0% { 
    opacity: 1; 
    transform: translateX(0px);
  }
}